button{
    background-color: color(primary, base); 
    border: 0;
    padding: .7rem 1rem;
    border-radius: .5rem;
    color: color(text);
    font-weight: 700;
    cursor: pointer;
}

.scroll-top{
    position: fixed;
    bottom: 4rem;
    right: 1rem;
    font-size: 3rem;
    cursor: pointer;
}